<template>
  <div>
   <custom-scan @decode="onDecode" />
  </div>
</template>
<script>
import CustomScan from '@/components/CustomScan/index.vue'
import eventBus from '@/utils/eventBus'
export default {
  name: 'ScanIndex',
  components: { CustomScan },
  data() {
    return {
    }
  },
  methods: {
    onDecode(result) {
      eventBus.$emit('ScanResult', result)
      this.$router.go(-1)
    }
  }
}
</script>
